import { Component, OnInit } from '@angular/core';
import {HttpService} from '../http.service';
import {ActivatedRoute, Params} from '@angular/router';
import {Router} from '@angular/router';

@Component({
  selector: 'app-single-pet',
  templateUrl: './single-pet.component.html',
  styleUrls: ['./single-pet.component.scss']
})
export class SinglePetComponent implements OnInit {
  pet:any;
  petId:any;

  constructor(private _httpService: HttpService, private _route:ActivatedRoute, private router: Router) { }

  ngOnInit() {
    this._route.params.subscribe((params: Params) => {
      console.log(params['id'])
      this.petId = params['id']
    })
    this.showPet();
  }

  showPet(){
    this._httpService.showPet(this.petId).subscribe(data => {
      console.log('Here is the single pet', data)
      this.pet = data
    })
  }

  Delete(petId){
    this._httpService.deletePet(petId).subscribe(data=>{
      console.log('Deleted Author', data)
      this.router.navigate(['/'])
    })
  }

}
